.homeBackground {
  width: 100vw;
  height: 100vh;
  background: url("../../eggs-1577626821823-1090.jpg") no-repeat;
  background-size: cover;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.startWindow {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.startWindow .helloWord {
  font-family: Lobster,cursive;
  color: #fdb515;
  padding: 10vh 0;
  font-size: 80px;
  margin: 0;
}
.startWindow .serch {
  width: 20%;
  height: 48vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.startWindow .serch .button {
  background: none;
  padding: 0px 25px;
  border-radius: 10px;
  cursor: pointer;
}
.startWindow .serch .button .text {
  color: #000;
  font-family: Lobster,cursive;
  font-size: 35px;
  text-decoration: none;
}
.homeContainer {
  width: 100%;
  min-height: 80vh;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.homeContainer .title {
  color: #fdb515;
  font-family: Lobster,cursive;
  font-size: 50px;
}
.homeContainer .description {
  color: #686868;
  font-family: EB Garamond,serif;
  font-size: 22px;
  width: 80%;
}
@media screen and (max-width: 800px) {
  .homeBackground {
    background-position: left -120px top;
  }
  .startWindow .helloWord {
    padding: 10vh 0;
    font-size: 55px;
  }
}
@media screen and (max-width: 530px) {
  .homeBackground {
    background-position: left -120px top;
  }
  .startWindow .helloWord {
    padding: 10vh 0;
    font-size: 35px;
  }
}
