.header {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
}
.logoContain {
  width: 20%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
}
.logoLink {
  font-family: Pacifico,cursive;
  color: #fff;
  font-size: 35px;
  text-decoration: none;
}
.burgerMenu {
  display: none;
}
.arrayLink {
  display: flex;
  justify-content: flex-end;
}
.arrayLink .linkContain {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.arrayLink .linkContain:hover .linkitem {
  color: #fdb515;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.arrayLink .linkContain:hover .burgerBlock {
  display: flex;
}
.arrayLink .linkContain .linkitem {
  position: relative;
  color: #fff;
  font-size: 30px;
  font-family: Pacifico,cursive;
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.arrayLink .linkContain .linkitem-active {
  color: #fdb515;
  font-size: 35px;
}
.arrayLink .linkContain .burgerBlock {
  display: none;
  position: absolute;
  top: 8.2vh;
  z-index: 10;
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.6);
  background: #fff;
}
.arrayLink .linkContain .burgerBlock .burgerContain {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 20px 30px;
}
.arrayLink .linkContain .burgerBlock .burgerContain .titleBurgerList {
  font-family: EB Garamond,serif;
  font-size: 16px;
  padding-bottom: 10px;
  cursor: pointer;
}
.arrayLink .linkContain .burgerBlock .burgerContain .burgerListItems {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.arrayLink .linkContain .burgerBlock .burgerContain .burgerListItems .itemBurgerList {
  font-family: EB Garamond,serif;
  font-size: 14px;
  color: #686868;
  padding: 5px 0;
  cursor: pointer;
  -webkit-transition: all 0,3s;
  transition: all 0, 3s;
}
.arrayLink .linkContain .burgerBlock .burgerContain .burgerListItems .itemBurgerList:hover {
  color: #000;
}
@media screen and (max-width: 1300px) {
  .arrayLink {
    display: none;
  }
  .burgerMenu {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 20px;
  }
  .burgerMenu .burgerIcon {
    width: 35px;
    height: 35px;
    background: url(./burger.svg) no-repeat;
    background-size: 100% 100%;
  }
  .burgerMenuBlock {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #1b3142;
    display: flex;
    flex-direction: column;
  }
  .burgerMenuBlock .burgerMenuHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
    background: #fff;
  }
  .burgerMenuBlock .burgerMenuHeader h3 {
    font-size: 20px;
    font-family: Pacifico, cursive;
  }
  .burgerMenuBlock .burgerMenuHeader span {
    width: 25px;
    height: 25px;
    background: url(./restaurant.svg) no-repeat;
    background-size: 100% 100%;
  }
  .burgerMenuBlock .burgerMenuContant {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80%;
  }
  .burgerMenuBlock .burgerMenuContant .burgerMenuLinkList {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .burgerMenuBlock .burgerMenuContant .burgerMenuLinkList .linkContain .linkitem {
    font-size: 35px;
    font-family: Pacifico,cursive;
    color: #fff;
    text-decoration: none;
  }
  .burgerMenuBlock .burgerMenuContant .burgerMenuLinkList .linkContain .linkitem-active {
    color: #fdb515;
    font-size: 35px;
  }
}
