.сontactsBackground {
  width: 100vw;
  height: 100vh;
  background: url("../../eggs-1577626821823-1090.jpg") no-repeat;
  background-size: cover;
  overflow-y: auto;
  scroll-behavior: smooth;
}
.сontactsStartWindow {
  width: 100%;
  height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}
.сontactsContainer {
  width: 100%;
  min-height: 71.8vh;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.сontactsContainer .title {
  color: #fdb515;
  font-family: Lobster,cursive;
  font-size: 50px;
}
.сontactsContainer .description {
  color: #686868;
  font-family: EB Garamond,serif;
  font-size: 22px;
  width: 80%;
}
