.searchBackground {
  width: 100%;
  height: 100%;
  background: url("../../eggs-1577626821823-1090.jpg") no-repeat;
  background-size: cover;
  overflow-y: auto;
  scroll-behavior: smooth;
}
.searchContainer {
  width: 100%;
  min-height: 71.8vh;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  background: #fff;
  display: flex;
  padding-top: 5%;
}
.searchContainer .title {
  color: #fdb515;
  font-family: Lobster,cursive;
  font-size: 50px;
}
.searchContainer .description {
  color: #686868;
  font-family: EB Garamond,serif;
  font-size: 22px;
  width: 80%;
}
.searchContainer .filterBlock {
  width: 20%;
  min-height: 15%;
  border-right: 1px solid #387898;
  margin-right: 2%;
  margin-left: 1%;
}
.searchContainer .renderBlock {
  width: 80%;
  height: 500px;
  margin-right: 2%;
  display: flex;
  flex-wrap: wrap;
}
