.searchCard {
  margin: 5px;
  width: 22%;
  border: 2px solid #000;
  position: relative;
}
.searchCard .topCard {
  position: relative;
  width: 100%;
}
.searchCard .topCard img {
  width: 100%;
  background-size: cover;
}
.searchCard .topCard .circleBlock {
  width: 90%;
  display: flex;
  justify-content: flex-end;
  position: absolute;
  bottom: -25px;
}
.searchCard .topCard .circleBlock .circle {
  width: 50px;
  height: 50px;
  background: #fff;
  border-radius: 50%;
}
.searchCard .topCard .circleBlock .circleSmall {
  position: absolute;
  top: -5px;
  right: 55px;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
}
.searchCard .topCard .circleBlock .circleSmall2 {
  position: absolute;
  top: -5px;
  right: -15px;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
}
.searchCard .topCard .circleBlock .circleSmall3 {
  position: absolute;
  top: -20px;
  right: 20px;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
}
.searchCard .bottomCard {
  display: flex;
  flex-direction: column;
  padding: 10px 20px;
}
